.about-container{
    width: 100%;
    height: 450px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0px 50px 0px;
}

.about-wrapper{
    width: 80%;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.section-header{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 40px 0px 20px 0px;
}

.section-header h1{
    width: 100%;
    text-align: center;
    font-size: 58px;
    font-family: 'Roboto';
    color: white;
    font-weight: bolder;
    margin: 0px 0px 0px 0px;
}

.section-header hr{
    background-image:radial-gradient(circle at top left,#fd00da, #19d7f8);
    width: 100px;
    height: 4px;
    border: none;
    /* border: solid 2px #d81159; */
    margin: 30px 0px 0px 0px;
}

.article-content{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.article-section{
    width: 80%;
}

.article-section h3{
    width: 100%;
    font-family: 'Roboto';
    font-size: 28px;
    color: white;
    font-weight: 900;
    margin: 10px 10px 20px 0px;
    padding: 0px;
    text-align: center;
}

.article-section p{
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 18px;
    color: white;
    margin: 0px;
    padding: 0px;
    line-height: 25px;
    text-align: center;
}